import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/home'
import Login from '@/login'
import Register from '@/register'
import SetPassword from '@/SetPassword'
import Index from '../page/index'
import Pay from '../page/orders/pay'
import HistoryEarn from '../page/my/historyEarn'
import TodayEarn from '../page/my/todayEarn'
import Orders from '../page/orders/ordersPage'
import OrdersAll from '../page/orders/ordersAll'
import OrdersAwait from '../page/orders/ordersAwait'
import OrdersHanding from '../page/orders/ordersHanding'
import OrdersDetail from '../page/orders/ordersDetail'
import OrdersCommit from '../page/orders/ordersCommit'
import OrdersPay from '../page/orders/ordersPay'
import OrdersItem from '../page/orders/ordersItem'
import ProductDetail from '../page/products/productDetail'
import ShopDetail from '../page/products/ShopDetail'
import Users from '../page/my/users'
import Voucher from '../page/my/voucher'
import Coupon from '../page/my/coupon'
import UpdateService from '../page/my/updateService'
import MessagePage from '../page/my/messagePage'
import MessageCenter from '../page/my/messageCenter'
import Share from '../page/my/share'
import Withdraw from '../page/my/withdraw'
import WithdrawCash from '../page/my/withdrawCash'
import Wallet from '../page/my/myWallet'
import Collect from '../page/my/collect'
import Team from '../page/my/team'
import Success from '../page/orders/success'
import FoodPage from '../page/products/foodPage'
import AllMenu from '../page/products/allMenu'
import PhotoAlbum from '../page/products/photoAlbum'
import PicView from '../components/picView'
import Comment from '../page/products/comment'
import CommentDetail from '../page/products/commentDetail'
import ProductList from '../page/products/productList'
import ChangeCity from '../page/changeCity'
import Download from '../page/download'
import Search from '../page/search'
import SearchList from '../page/searchList'
import NoLogin from '../page/noLogin'
import UserInfo from '../page/my/userInfo'
import MyIdentity from '../page/my/myIdentity'
import VoucherList from '../page/products/voucherList'
import VoucherDetail from '../page/products/voucherDetail'
import VoucherMore from '../page/products/voucherMore'
import ChangeName from '../page/my/changeName'
import RedPacketList from '../page/products/redPacketList'
import VoucherChange from '../page/products/VoucherChange'
import RedPacketChange from '../page/products/redPacketChange'
import RedPacketPasted from '../page/products/redPacketPasted'
import BuySuccess from '../page/products/buySuccess'
import ReceiveRed from '../page/receiveRed'
import ProblemFeedback from '../page/orders/problemFeedback'
// import MessagePage from '../page/messagePage'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/login', name: '', component: Login, meta: {title: ''} },
    { path: '/register', name: '', component: Register, meta: {title: ''} },
    { path: '/setPassword', name: '', component: SetPassword, meta: {title: ''} },
    { path:'/historyEarn', name:'', component: HistoryEarn, meta: {title: ''} },
    { path:'/todayEarn', name:'', component: TodayEarn, meta: {title: ''} },
    { path:'/withdraw', name:'', component: Withdraw, meta: {title: ''} },
    { path:'/withdrawCash', name:'', component: WithdrawCash, meta: {title: ''} },
    { path:'/collect', name:'', component: Collect, meta: {title: ''} },
    { path:'/team', name:'', component: Team, meta: {title: ''} },
    { path:'/foodPage', name:'', component: FoodPage, meta: {title: ''}},
    { path:'/allMenu', name:'', component: AllMenu, meta: {title: ''}},
    { path:'/photoAlbum', name:'', component: PhotoAlbum, meta: {title: ''}},
    { path:'/picView', name:'', component: PicView, meta: {title: ''}},
    { path:'/changeCity', name:'', component: ChangeCity, meta: {title: ''}},
    { path:'/comment', name:'', component: Comment, meta: {title: ''}},
    { path:'/commentDetail', name:'', component: CommentDetail, meta: {title: ''}},
    { path:'/productList', name:'', component: ProductList, meta: {title: ''}},
    { path:'/search', name:'', component: Search, meta: {title: ''}},
    { path:'/searchList', name:'', component: SearchList, meta: {title: ''}},
    { path:'/ordersItem', name:'', component: OrdersItem, meta: {title: ''}},
    { path:'/noLogin', name:'', component: NoLogin, meta: {title: ''}},
    { path:'/voucherList', name:'', component: VoucherList, meta: {title: ''}},
    { path:'/voucherChange', name:'', component: VoucherChange, meta: {title: ''}},
    { path:'/voucherDetail', name:'', component: VoucherDetail, meta: {title: ''}},
    { path:'/voucherMore', name:'', component: VoucherMore, meta: {title: ''}},
    { path:'/buySuccess', name:'', component: BuySuccess, meta: {title: ''}},
    { path:'/receiveRed', name:'', component: ReceiveRed, meta: {title: ''}},
    { path:'/problemFeedback', name:'', component: ProblemFeedback, meta: {title: ''}},

    {
      path: '/',
      name: '',
      component: Home,
      children: [
        { path: '/', component: Index, meta: {title: ''} },
        { path: '/index', component: Index, meta: {title: ''} },
        { path: '/users', component: Users, meta: {title: ''} },
        { path: '/ordersDetail', name: '', component: OrdersDetail,meta: {title: ''}},
        { path: '/productDetail', name: '', component: ProductDetail,meta: {title: ''}},
        {
          path: '/ordersAll',
          name: '',
          component: Orders,
          children: [
            { path: '/ordersAll', component: OrdersAll, meta: {title: ''} },
            { path: '/ordersAwait', component: OrdersAwait, meta: {title: ''} },
            { path: '/ordersHanding', component: OrdersHanding, meta: {title: ''} },
          ]
        }
      ]
    },
    { path: '/shopDetail', name: '', component: ShopDetail, meta: {title: ''}},
    { path: '/share', name: '', component: Share, meta: {title: ''}},
    { path: '/wallet', name: '', component: Wallet, meta: {title: ''}},
    { path: '/ordersCommit', name: '', component: OrdersCommit, meta: {title: ''}},
    { path: '/ordersPay', name: '', component: OrdersPay, meta: {title: ''}},
    { path: '/pay', name: '', component: Pay, meta: {title: '',keepAlive: true}},
    { path:'/voucher', name:'', component:Voucher, meta: {title: ''} },
    { path:'/coupon', name:'', component:Coupon, meta: {title: ''} },
    { path:'/redPacketList', name:'', component:RedPacketList, meta: {title: ''} },
    { path:'/redPacketChange', name:'', component:RedPacketChange, meta: {title: ''} },
    { path:'/redPacketPasted', name:'', component:RedPacketPasted, meta: {title: ''} },
    { path:'/updateService', name:'', component:UpdateService, meta: {title: ''} },
    { path:'/messagePage', name:'', component:MessagePage, meta: {title: ''} },
    { path:'/messageCenter', name:'', component:MessageCenter, meta: {title: ''} },
    { path:'/success', name:'', component:Success, meta: {title: ''} },
    { path:'/download', name:'', component:Download, meta: {title: ''} },
    { path:'/userInfo', name:'', component:UserInfo, meta: {title: ''} },
    { path:'/myIdentity', name:'', component:MyIdentity, meta: {title: ''} },
    { path:'/changeName', name:'', component:ChangeName, meta: {title: ''} }


    // {
    //   path:'/messagePage',
    //   name:'',
    //   component:MessagePage
    // }
  ]
})



